/* Cookie Notification styles */
.cookie-notification {
  background-color: blue;
  color: #FFFFFF;
  padding: 10px;
  width: 100%; }
  .cookie-notification.-is-hidden {
    display: none; }
  @media all and (max-width: 1200px) {
    .cookie-notification {
      padding: 10px 20px; } }
  @media all and (max-width: 568px) {
    .cookie-notification {
      padding: 10px 15px; } }
  .cookie-notification p {
    margin: 0;
    line-height: 1.5rem; }
  .cookie-notification a {
    color: #FFFFFF;
